@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300);
.navbar {
    font-family: 'Poppins', sans-serif;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    width: 100%;
    min-height: 75px;
    padding: 1rem;
    background-color: black;
    color: #f4f4f4;
}

.navbar__logo {
    color: #f4f4f4;
}

.navbar__links {
    display: flex;
    justify-content: space-between;
    width: 70%;
}

.navbar__link {
    padding: 0 0.3rem;
}

.navbar__link:hover {
    color: rgb(141, 141, 203);
}

.navbar__burger {
    width: 40px;
    height: 50px;
    background: transparent;
    border: none;
    color: inherit;
}

ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

@media screen and (max-width:767px)
{
.navbar__links {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: fixed;
    right: -100vw;
    bottom: 0;
    width: 0;
    height: 100vh;
    visibility: hidden;
    background-color: black;
    transition: all 0.8s ease-in-out;
} 
.show-nav .navbar__links {
    width: 100vw;
    right: 0;
    visibility: visible;
}

/* bonus */
.navbar__item::after {
    content: "";
    display: block;
    margin: 0 auto;
    width: 10vw;
    height: 1px;
    background: #f4f4f4;
}

.navbar__item:last-child::after {
    display: none;
}

.navbar__link {
    display: block;
    padding: 1.5rem;
    font-size: 5vw;
}
.navbar__burger{
    display: block;
    position: fixed;
    top: 1rem;
    right: 1rem;
}
.navbar__burger:hover {
    cursor: pointer;
}
.burger-bar,
.burger-bar::before,
.burger-bar::after {
    display: block;
    width: 30px;
    height: 3px;
    position: relative;
    border-radius: 3px;
    background:#f4f4f4;
    transition: all .5s ease-in-out;
}
.burger-bar::before,
.burger-bar::after {
    content: "";
    position: absolute;
    left: 0;
}
.burger-bar::before {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
}
.burger-bar::after {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
}
.show-nav .burger-bar {
    width: 0;
    background: transparent;
}
.show-nav .burger-bar::before{
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
.show-nav .burger-bar::after {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}
/* bonus */
.navbar__item {
    -webkit-transform: translateY(-100vh);
            transform: translateY(-100vh);
}
.show-nav .navbar__item {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
.show-nav .slideInDown-1 {
    transition: all 1.4s ease-out;
}
.show-nav .slideInDown-2 {
    transition: all 1.3s ease-out;
}
.show-nav .slideInDown-3 {
    transition: all 1.2s ease-out;
}
.show-nav .slideInDown-4 {
    transition: all 1.1s ease-out;
}
.show-nav .slideInDown-5 {
    transition: all 1s ease-out;
}
}
