* {
    font-family: 'Poppins', sans-serif;
    margin: 0;
}

:root {
    /* couleurs */
    --body-bg-color: #FFFFFF;
    --sidebar-bg-color: #f4f4f4;
    --text-color: #333333;
    --title-color: #111111;
    --date-color: #1f94c2;
    --dark-body-bg-color: #242424;
    --dark-sidebar-bg-color: #171717;
    --dark-text-color: #d8d8d8;
    --dark-title-color: #ffffff;
    --dark-date-color: #e6ef71;


    /* --dark-body-bg-color */ 

    --icon-color:var(--text-color);
    --circle-color: #AAA;
    --circle-color-plain: #555;
    --dark-icon-color:var(--dark-text-color);
    --dark-circle-color: rgb(108, 108, 108);
    --dark-circle-color-plain: rgb(238, 238, 238);

    /* fonts */
    --h1-font-size: 1.7rem;
    --h2-font-size: 1.3rem;
    --h3-font-size: 1.1rem;
    --h4-font-size: 1rem;

    /* helpers */
    --spacing-1: .4rem;
    --spacing-2: .8rem;
    --spacing-3: 1.2rem;
    --spacing-4: 1.5rem;
    --spacing-5: 4rem;
}

a {
    text-decoration: none;
    color: inherit;
}

.dark {
    background-color: #242424;
    background-color: var(--dark-body-bg-color);
    color: #d8d8d8;
    color: var(--dark-text-color);
}

h1 {
    font-size: 1.7rem;
    font-size: var(--h1-font-size);
    text-transform: uppercase;
}

h2 {
    display: flex;
    align-items: center;
    margin-bottom: 1.5rem;
    margin-bottom: var(--spacing-4);
    font-size: 1.3rem;
    font-size: var(--h2-font-size);
    letter-spacing: 4px;
    text-transform: uppercase;
    margin-left: 1rem;
}

button {
    background-color: transparent;
    border: none;
    cursor: pointer;
}

.dark button {
    color: #d8d8d8;
    color: var(--dark-text-color);
}

.dark h1,
.dark h2,
.dark h3 {
    color: #ffffff;
    color: var(--dark-title-color);
}

li {
    margin-bottom: .8rem;
    margin-bottom: var(--spacing-2);
}

/* helper */
.mb3 {
    margin-bottom: 1.2rem;
    margin-bottom: var(--spacing-3);
}
.mb5 {
    margin-bottom: 4rem;
    margin-bottom: var(--spacing-5);
}

/* grids */
.grid__row {
    margin: 1.2rem;
    margin: var(--spacing-3);
}

.App {
    padding-top: 6rem;
}

.descriptprofil {
    text-align: center !important;
}

.descriptprofils {
    text-align: center;
    font-size: 25px;
    font-weight: 900;
    color: #197ea7;
}
@media screen and (min-width:768px) {
    
.grid_container {
    display: grid;
    grid-template-columns: 1fr 3fr;
}
.grid__row {
    display: grid;
    grid-template-columns: 1fr 2fr;
}
.App  {
   
    padding-top: 6rem;
}
}

