#loader
{
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    position: fixed;
    background-color: black;
    -webkit-animation: load 2.5s linear;
            animation: load 2.5s linear;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    pointer-events: none;
    z-index: 1;
}
@-webkit-keyframes load 
{
    0%{
        opacity: 1;
    }
    80%
    {
        opacity: 0.9;
    }
    100%
    {
        opacity: 0;
    }
}
@keyframes load 
{
    0%{
        opacity: 1;
    }
    80%
    {
        opacity: 0.9;
    }
    100%
    {
        opacity: 0;
    }
}
